.PlayerDetailsBody{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background: radial-gradient(circle, #2b1055, #090a1d);
    /* background-image:url("https://1.bp.blogspot.com/-Fo3iRt97ZXY/XvSG4EMwi0I/AAAAAAAAVEo/mrApRTcVVRk1m-fX9K-ffNwRUXlHUocdwCLcBGAsYHQ/s1600/h.jpg"); */
    font-family: "Times New Roman";
}

.playerNumberBox{
    position: absolute;
    right: 2rem;
    top: 2rem;
}

.fancy-shadow-card { 
    box-shadow: rgba(240, 46, 170, 0.4) 5px 5px, rgba(240, 46, 170, 0.3) 10px 10px, rgba(240, 46, 170, 0.2) 15px 15px, rgba(240, 46, 170, 0.1) 20px 20px, rgba(240, 46, 170, 0.05) 25px 25px;
      background: #fff;
      border-radius: 5px;
      padding: 5px;
      display: flex;
      flex-direction: column;
      scale: 2;
    }
    .card-title {
      font-weight: 800;
    }

.PlayerDetailscontainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }

  .PlayerDetailscontainerBox{
    display: flex;
    width: 100%;
  }
  
  /* Left side (image) styling */
  .PlayerDetailsleft-side {
    flex: 1;
    display: flex; /* Centers the image */
    justify-content: center; /* Horizontally centers the image */
    align-items: center; /* Vertically centers the image */
  }
  
  .PlayerDetailsphoto {
    height: 90%;
    width: 90%;
    object-fit: cover;
    border: 10px solid #fa7373;
    border-radius: 10%;
    animation: fadeIn 2s ease-out, moveUp 2s ease-out;
    transition: transform 0.3s ease, filter 0.3s ease;
  }

  /* .PlayerDetailsphoto:hover {
    transform: scale(1.08); 
  } */
  
  .PlayerDetailsright-side {
    flex: 1; 
    display: flex; 
    justify-content: center;
  }

  .typography-container {
    animation: fadeIn 2s ease-out, moveUp 2s ease-out;
    display: flex;
    flex-direction: column;
    gap:1rem;
    justify-content: center;
    align-self: center;

  }
  
  .typographyText {
    margin: 0;
    background: linear-gradient(90deg, #ff7f50, #ff4500, #ff6347, #ffa500);
    /* background: #ffffff; */
    background-clip: text;
    color: transparent;
    cursor: pointer;
    transition: transform 0.3s ease, filter 0.3s ease;
    text-align: top;
    white-space: nowrap;
    text-align: center;
  }

  .nickName{
    font-size: 13rem;
    font-weight: 900;
    margin: 0;
    padding: 0;
  }

  .fullName{
    font-size: 4rem;
    font-weight: 700;
  }

  .address{
    font-size: 3rem;
    font-weight: 600;
  }

  .playerskill{
    font-size: 2.5rem;
    font-weight: 500;
  }
  
  .typographyText:hover {
    transform: scale(1.08); 
  }
  
  .typographyText:active {
    transform: scale(1.05);
  }


  @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  @keyframes moveUp {
    0% { transform: translateY(30px); }
    100% { transform: translateY(0); }
  }








