body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-container {
  text-align: center;
  padding: 20px;
}

.search-bar {
  width: 80%;
  max-width: 400px;
  padding: 10px;
  margin: 15px auto;
  border: 1px solid #ddd;
  border-radius: 5px;
  text-align: center;
}

.player-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  padding: 20px;
}

.player-card {
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  background-color: #f9f9f9;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.player-photo {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 10px;
}

.player-grid-header{
  display: flex;
  justify-content: center;
  flex-direction: column;
}









.profile-card {
  position: relative;
  padding: 2.5rem;
  background: rgba(30, 41, 59, 0.8);
  backdrop-filter: blur(10px);
  border-radius: 1.5rem;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.profile-card:hover {
  transform: translateY(-5px);
}

.card-content {
  position: relative;
  z-index: 2;
}

/* Avatar Styles */
.avatar-wrapper {
  position: relative;
  width: 265px;
  height: 265px;
  margin: 0 auto 2rem;
}

.avatar {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
}

.avatar-inner {
  position: absolute;
  inset: 0;
  background: linear-gradient(45deg, #60a5fa, #a78bfa);
  opacity: 0.8;
}

.avatar-glow {
  position: absolute;
  inset: -2px;
  background: linear-gradient(45deg, #60a5fa, #a78bfa);
  border-radius: 50%;
  opacity: 0;
  filter: blur(15px);
  transition: opacity 0.3s ease;
}

.avatar:hover .avatar-glow {
  opacity: 0.5;
}

.avatar-border {
  position: absolute;
  inset: 0;
  border: 3px solid transparent;
  border-radius: 50%;
  background: linear-gradient(45deg, #60a5fa, #a78bfa) border-box;
  -webkit-mask: 
      linear-gradient(#fff 0 0) padding-box,
      linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
}

/* Profile Info */
.profile-info {
  text-align: center;
}

.name {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  background: linear-gradient(to right, #60a5fa, #a78bfa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.title {
  color: #94a3b8;
  font-size: 1rem;
  margin-bottom: 2rem;
}

/* Stats Section */
.stats {
  display: flex;
  justify-content: space-around;
  margin-bottom: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.stat {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.stat-value {
  font-size: 1.2rem;
  font-weight: bold;
  background: linear-gradient(to right, #60a5fa, #a78bfa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.stat-label {
  font-size: 0.9rem;
  color: #94a3b8;
}

/* Bio Section */
.bio {
  color: #94a3b8;
  line-height: 1.6;
  margin-bottom: 2rem;
  padding: 0 1rem;
}

/* Skills Section */
.skills {
  display: flex;
  gap: 0.8rem;
  justify-content: center;
  margin-bottom: 2rem;
}

.skill {
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 2rem;
  font-size: 0.9rem;
  color: #94a3b8;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.skill:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  transform: translateY(-2px);
}

/* Action Buttons */
.actions {
  display: flex;
  gap: 1rem;
}

.action-btn {
  flex: 1;
  position: relative;
  padding: 0.8rem;
  border: none;
  border-radius: 0.8rem;
  font-size: 1rem;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s ease;
}

.action-btn span {
  position: relative;
  z-index: 1;
}

.btn-effect {
  position: absolute;
  inset: 0;
  transition: opacity 0.3s ease;
}

.primary {
  background: linear-gradient(45deg, #60a5fa20, #a78bfa20);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.primary .btn-effect {
  background: linear-gradient(45deg, #60a5fa, #a78bfa);
  opacity: 0;
}

.primary:hover .btn-effect {
  opacity: 0.2;
}

.secondary {
  background: transparent;
  color: #94a3b8;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.secondary .btn-effect {
  background: rgba(255, 255, 255, 0.1);
  opacity: 0;
}

.secondary:hover .btn-effect {
  opacity: 1;
}

/* Card Effects */
.card-shine {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
      90deg,
      transparent,
      rgba(255, 255, 255, 0.1),
      transparent
  );
  animation: shine 8s infinite;
}

@keyframes shine {
  to {
      left: 200%;
  }
}

.card-border {
  position: absolute;
  inset: 0;
  border: 2px solid transparent;
  border-radius: 1.5rem;
  background: linear-gradient(45deg, #60a5fa, #a78bfa) border-box;
  -webkit-mask: 
      linear-gradient(#fff 0 0) padding-box,
      linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.profile-card:hover .card-border {
  opacity: 1;
}

.card-glow {
  position: absolute;
  inset: 0;
  background: linear-gradient(45deg, #60a5fa, #a78bfa);
  border-radius: 1.5rem;
  opacity: 0;
  filter: blur(20px);
  transition: opacity 0.3s ease;
}

.profile-card:hover .card-glow {
  opacity: 0.05;
}

/* Responsive Design */
@media (max-width: 480px) {
  .container {
      padding: 1rem;
  }

  .profile-card {
      padding: 1.5rem;
  }

  .avatar-wrapper {
      width: 100px;
      height: 100px;
  }

  .name {
      font-size: 1.5rem;
  }

  .skills {
      flex-wrap: wrap;
  }
}


